import Header from "./Components/Header/Header";
import axios from "axios";
import React, { useEffect, useState } from "react";
import * as solanaWeb3 from "@solana/web3.js"; // 1.36.0
import * as splToken from "@solana/spl-token"; // 0.1.8
// import { Connection, PublicKey } from "@solana/web3.js";
// import { TOKEN_PROGRAM_ID } from "@solana/spl-token";
function App() {
  const [isSolanaNftData, setIsSolanaNftData] = useState([]);
  const [address, setAddress] = useState(localStorage.getItem("walletValue"));
  const connectToPhantom = async () => {
    const resp = await window?.solana?.connect();
    // console.log("Connected with wallet address: ", resp.publicKey.toString());
    localStorage.setItem("walletValue", resp.publicKey.toString());
    setAddress(localStorage.getItem("walletValue"));
  };

  // get Devnet-solana nftItems
  const solanaNft = async () => {
    const resp = await window?.solana?.connect();

    const config = {
      headers: {
        "Content-Type": "application/json",
      },
    };
    const data = {
      jsonrpc: "2.0",
      method: "qn_fetchNFTs",
      params: {
        wallet: resp.publicKey.toString(),
        omitFields: ["provenance", "traits"],
      },
    };
    axios
      .post(
        "https://small-chaotic-paper.solana-mainnet.discover.quiknode.pro/e7883b6278111e0164f776d1c4edcbfb7136dd03/",
        data,
        config
      )
      .then(function (response) {
        // handle success
        console.log(response.data.result.assets, "solana");
        setIsSolanaNftData(response.data.result.assets);
      })
      .catch((err) => {
        // handle error
        console.log(err);
      });
  };

  useEffect(() => {
    solanaNft();
  }, []);

  // console.log("isSolanaNftData", isSolanaNftData);
  return (
    <Header
      nftItems={isSolanaNftData}
      onChangeClick={connectToPhantom}
      solWallet={address}
    />
  );
}

export default App;
